import { ComponentStylingModel, ComponentStylingPropertiesControlComponent, genericPropertiesControlFor } from '@anglr/dynamic/layout-editor';
import { MaterialSelectModel } from './select.model';
/**
 * Material select layout metadata
 */

export class MaterialSelectLayoutEditorMetadata {
  //######################### constructor #########################
  constructor() {
    //######################### public properties - implementation of LayoutEditorMetadataDescriptor #########################

    /**
     * @inheritdoc
     */
    this.metaInfo = {
      name: 'Select',
      description: 'Material select',
      optionsMetadata: {
        propertiesMetadata: [{
          modelType: ComponentStylingModel,
          propertiesControls: [ComponentStylingPropertiesControlComponent]
        }, {
          modelType: MaterialSelectModel,
          propertiesControls: [genericPropertiesControlFor(['label', 'placeholder', 'hint', 'appearance', 'multiple', 'controlName'])]
        }]
      },
      defaultOptions: {
        label: 'Input label',
        placeholder: 'Placeholder',
        hint: 'Hint',
        multiple: false
      },
      group: 'Material form fields'
    };
    Object.freeze(this);
  }

}